import React from 'react' 
import {BrowserRouter as Router, Route, Switch, Link} from 'react-router-dom';
import {Navbar, Nav, Form, FormControl, Button} from 'react-bootstrap'
import 'bootstrap/dist/css/bootstrap.min.css';
import  Home  from './Home'
import  Login  from './Login'
import  About  from './About'
import Registrar from './Registrar'
import Contactus from './Contactus'
import  PageNotFound  from './PageNotFound'

const Menu = () => {
    return (
        <Router basename={process.env.PUBLIC_URL}>
            <Navbar bg="dark" variant="dark" sticky="top">
                <Link to="/Home">
                <img
                    src="https://img.icons8.com/external-vitaliy-gorbachev-lineal-color-vitaly-gorbachev/50/000000/external-space-junk-space-vitaliy-gorbachev-lineal-color-vitaly-gorbachev.png"
                    width="30"
                    height="30"
                    className="d-inline-block align-top"
                    alt="Logo de la pagina"
                />
                    Space
                </Link>
                <Nav className="mr-auto">
                    <Link to="/about">About</Link>
                    <Link to="/contactus">Contact Us</Link>
                </Nav>
                <Form inline>                    
                    <FormControl type="text" placeholder="Search" className="mr-sm-2"/>
                    <Button variant ="outline-info">Search</Button>
                    <Link to="/login">Ingresar</Link>
                    <Link to="/registrar">Registrarse</Link>
                </Form>
            </Navbar>
            <Switch>
                <Route path="/" exact component={Home}/>
                <Route path="/login/" exact component={Login}/>
                <Route path="/registrar/" exact component={Registrar}/>
                <Route path="/about/" component={About} />
                <Route path="/contactus/" component={Contactus}/> 
                <Route component={PageNotFound}/> 
            </Switch>
        </Router>
    );
}
export default Menu